

/* DARK/LIGHT THEME VARIABLES - DEEP BLACK */
:root {
  --bg: #ffffff;
  --side: #f0f0f0;
  --card: #ffffff;
  --text: #000000;
  --border: #dddddd;
  --accent: #2563eb;
}

[data-theme="dark"] {
  --bg: #000000;
  --side: #0f0f0f;
  --card: #1a1a1a;
  --text: #ffffff;
  --border: #222222;
  --accent: #3b82f6;
}

* { box-sizing: border-box; }
body, html { margin: 0; padding: 0; height: 100vh; width: 100vw; overflow: hidden; font-family: 'Inter', sans-serif; }

.app-shell { display: flex; height: 100vh; width: 100vw; background: var(--bg); color: var(--text); }

/* SIDEBAR - ZERO GAP */
.sidebar {
  width: 280px;
  background: var(--side);
  border-right: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.sidebar-top { padding: 20px; border-bottom: 1px solid var(--border); display: flex; justify-content: space-between; align-items: center; }
.brand { font-size: 22px; font-weight: 800; color: var(--accent); }

.sidebar-scrollable { flex: 1; overflow-y: auto; padding: 20px; }
.control-group h3, .saved-places h3 { font-size: 14px; text-transform: uppercase; letter-spacing: 1px; margin-bottom: 15px; color: var(--accent); }

.field { margin-bottom: 12px; }
.field label { display: block; font-size: 11px; margin-bottom: 4px; opacity: 0.7; }
.field input { width: 100%; padding: 8px; background: var(--bg); border: 1px solid var(--border); color: var(--text); border-radius: 4px; }

.btn-analyze { width: 100%; background: var(--accent); color: white; border: none; padding: 12px; border-radius: 4px; font-weight: 700; cursor: pointer; margin: 10px 0; }
.btn-save { width: 100%; background: transparent; border: 1px solid var(--border); color: var(--text); padding: 10px; border-radius: 4px; cursor: pointer; }

.place-item { padding: 10px; border-bottom: 1px solid var(--border); cursor: pointer; }
.place-item:hover { background: var(--border); }
.place-item span { display: block; font-size: 11px; opacity: 0.6; }

.sidebar-footer { padding: 20px; background: var(--card); border-top: 1px solid var(--border); }
.team-grid { display: grid; grid-template-columns: 1fr 1fr; gap: 8px; font-size: 11px; margin: 10px 0; }
.guide-tag { font-weight: bold; font-size: 12px; border-top: 1px dashed var(--border); padding-top: 10px; }

/* RIGHT CONTENT - STACKED */
.main-content { flex: 1; display: flex; flex-direction: column; height: 100vh; }
.map-container { height: 50vh; width: 100%; border-bottom: 1px solid var(--border); }
.results-container { height: 50vh; width: 100%; overflow-y: auto; background: var(--bg); padding: 25px; }

/* TWO COLUMN LAYOUT FOR RESULTS */
.results-grid { display: grid; grid-template-columns: 380px 1fr; gap: 25px; max-width: 1400px; margin: 0 auto; }
.card { background: var(--card); border: 1px solid var(--border); padding: 20px; border-radius: 12px; margin-bottom: 20px; }

/* .score-value { font-size: 72px; font-weight: 900; color: var(--accent); text-align: center; line-height: 1; margin: 10px 0; } */
.status-pill { text-align: center; padding: 6px; border-radius: 20px; font-size: 13px; font-weight: bold; text-transform: uppercase; }
.status-pill.low { background: #10b981; color: white; }
.status-pill.moderate { background: #f59e0b; color: white; }
.status-pill.high { background: #ef4444; color: white; }

.risk-item { border-left: 4px solid #ef4444; padding-left: 15px; margin-bottom: 15px; font-size: 14px; }
.evidence-entry { border-bottom: 1px solid var(--border); padding-bottom: 15px; margin-bottom: 15px; }
.evidence-entry p { font-size: 13px; line-height: 1.5; margin: 5px 0; }

.empty-results { height: 100%; display: flex; align-items: center; justify-content: center; color: var(--border); font-size: 20px; }

/* Custom Scrollbars */
::-webkit-scrollbar { width: 5px; height: 5px; }
::-webkit-scrollbar-thumb { background: var(--border); border-radius: 10px; }




