/* Glassmorphic Brand Container */
.sidebar-top {
  padding: 20px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.brand {
  font-size: 26px;
  font-weight: 800;
  letter-spacing: -1px;
  padding: 10px 18px;
  
  /* The Glass Effect */
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 14px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);

  /* The Gradient Text */
  background-image: linear-gradient(135deg, #3b82f6 0%, #ffffff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Subtle Glow behind the text */
.brand::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  height: 80%;
  background: rgba(59, 130, 246, 0.2);
  filter: blur(25px);
  transform: translate(-50%, -50%);
  z-index: -1;
}




/* --- THE NEW TEAK ACCENT & HOVER STYLES --- */
:root {
  --accent: #2d8a8a; /* Deep Teak / Seafoam Teal */
  --accent-hover: #1f6363;
}

/* 1. TITLE REFINEMENT (Visible in both modes) */
.brand {
  display: flex !important;
  align-items: center !important;
  gap: 12px !important;
  padding: 10px 20px !important;
  border-radius: 12px !important;
  font-size: 26px !important;
  font-weight: 800 !important;
  
  /* Glassmorphism that works in light & dark */
  background: rgba(45, 138, 138, 0.05) !important;
  backdrop-filter: blur(10px) !important;
  border: 1px solid rgba(45, 138, 138, 0.2) !important;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1) !important;
  
  /* Gradient Text - Darker bottom for light mode visibility */
  background-image: linear-gradient(135deg, var(--accent) 0%, #114444 100%) !important;
  -webkit-background-clip: text !important;
  background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
}

/* Dot Indicator using Teak */
.brand-dot {
  width: 10px;
  height: 10px;
  background: var(--accent);
  border-radius: 50%;
  box-shadow: 0 0 12px var(--accent);
}

/* 2. SAVE PLACE BUTTON - MODERN HOVER */
.btn-save {
  width: 100%;
  background: transparent !important;
  border: 1px solid var(--accent) !important;
  color: var(--text) !important;
  padding: 12px !important;
  border-radius: 8px !important;
  font-weight: 600 !important;
  cursor: pointer !important;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  gap: 8px !important;
}

.btn-save:hover {
  background: var(--accent) !important;
  color: white !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(45, 138, 138, 0.3) !important;
  letter-spacing: 0.5px !important;
}

.btn-save:active {
  transform: translateY(0) !important;
}

/* 3. UPDATING OTHER BLUE ELEMENTS TO TEAK */
.btn-analyze {
  background: var(--accent) !important;
  transition: background 0.3s ease !important;
}
.btn-analyze:hover {
  background: var(--accent-hover) !important;
}







/* --- FORCED THEME REFINEMENTS --- */

