}





/* --- FULL RESPONSIVENESS (MOBILE & TABLETS) --- */
@media (max-width: 1024px) {
    /* 1. Shell Reset: Allow the page to scroll naturally */
    .app-shell {
        flex-direction: column !important;
        overflow-y: auto !important;
        height: auto !important;
        position: relative !important;
    }

    /* 2. Sidebar: Make it full width and remove fixed positioning */
    .sidebar {
        width: 100% !important;
        height: auto !important;
        border-right: none !important;
        border-bottom: 1px solid var(--border) !important;
    }

    /* Remove the scroll lock on sidebar internal sections */
    .sidebar-scrollable {
        overflow: visible !important;
        height: auto !important;
    }

    .places-list {
        max-height: 250px !important; /* Limit list height so it doesn't push team card too far down */
        overflow-y: auto !important;
    }

    /* 3. Main Content: Stack the map and results */
    .main-content {
        height: auto !important;
        overflow: visible !important;
    }

    .map-container {
        height: 400px !important; /* Fixed height for map on mobile */
        min-height: 300px !important;
    }

    .results-container {
        height: auto !important;
        padding: 15px !important;
    }

    /* 4. Results Grid: Convert 2-columns to 1-column */
    .results-grid {
        display: flex !important;
        flex-direction: column !important;
        gap: 15px !important;
        padding: 0 !important;
    }

    .col-1, .col-2 {
        width: 100% !important;
    }

    /* 5. UI Adjustments for small touch targets */
    .score-value {
        font-size: 60px !important; /* Slightly smaller text for mobile */
    }

    .brand {
        justify-content: center !important;
        margin: 0 auto !important;
    }

    .sidebar-top {
        flex-direction: row !important;
        justify-content: space-between !important;
        align-items: center !important;
    }

    /* 6. Fix for Team Card spacing on mobile */
    .sidebar-footer {
        margin: 20px 15px !important;
    }
}

/* Specific fix for very small screens (phones) */
@media (max-width: 480px) {
    .team-grid {
        grid-template-columns: 1fr !important; /* Stack names in one column */
    }
    
    .status-pill {
        font-size: 11px !important;
        letter-spacing: 0px !important;
    }
}



