  --text: #000000;
  --border: #dddddd;
  --accent: #2563eb;
}

[data-theme="dark"] {
  --bg: #000000;
  --side: #0f0f0f;
  --card: #1a1a1a;
  --text: #ffffff;
  --border: #222222;
  --accent: #3b82f6;
}

* { box-sizing: border-box; }
body, html { margin: 0; padding: 0; height: 100vh; width: 100vw; overflow: hidden; font-family: 'Inter', sans-serif; }

.app-shell { display: flex; height: 100vh; width: 100vw; background: var(--bg); color: var(--text); }

/* SIDEBAR - ZERO GAP */
.sidebar {
  width: 280px;
  background: var(--side);
  border-right: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

