.sidebar-scrollable { flex: 1; overflow-y: auto; padding: 20px; }
.control-group h3, .saved-places h3 { font-size: 14px; text-transform: uppercase; letter-spacing: 1px; margin-bottom: 15px; color: var(--accent); }

.field { margin-bottom: 12px; }
.field label { display: block; font-size: 11px; margin-bottom: 4px; opacity: 0.7; }
.field input { width: 100%; padding: 8px; background: var(--bg); border: 1px solid var(--border); color: var(--text); border-radius: 4px; }

.btn-analyze { width: 100%; background: var(--accent); color: white; border: none; padding: 12px; border-radius: 4px; font-weight: 700; cursor: pointer; margin: 10px 0; }
.btn-save { width: 100%; background: transparent; border: 1px solid var(--border); color: var(--text); padding: 10px; border-radius: 4px; cursor: pointer; }

