/* Fullscreen Tab Viewport - Fixed Border Issue */
.tab-viewport.fullscreen {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  z-index: 99999 !important;
  background: var(--bg, #0f172a);
  overflow-y: auto;
  animation: fadeIn 0.3s ease-in-out;
  padding-top: 80px !important;
  border: none !important;
  outline: none !important;
  box-shadow: none !important;
}

/* Hide all GeoGPT elements and other overlays ONLY when in fullscreen */
.tab-viewport.fullscreen ~ .geogpt-fixed-container,
.tab-viewport.fullscreen ~ * .geogpt-fixed-container,
.tab-viewport.fullscreen .geogpt-fixed-container,
.tab-viewport.fullscreen ~ *:not(.results-tab-bar):not(.floating-close-btn),
.tab-viewport.fullscreen ~ .map-container ~ *:not(.results-tab-bar):not(.floating-close-btn),
.tab-viewport.fullscreen ~ .main-content > *:not(.results-container):not(.results-tab-bar):not(.floating-close-btn) {
  display: none !important;
  visibility: hidden !important;
  opacity: 0 !important;
  z-index: -9999 !important;
  position: absolute !important;
  left: -9999px !important;
  top: -9999px !important;
  width: 0 !important;
  height: 0 !important;
  overflow: hidden !important;
  pointer-events: none !important;
  transform: scale(0) !important;
}

/* Ensure GeoGPT is completely gone in fullscreen */
body:has(.tab-viewport.fullscreen) .geogpt-fixed-container,
body:has(.tab-viewport.fullscreen) * .geogpt-fixed-container {
  display: none !important;
  visibility: hidden !important;
  opacity: 0 !important;
  z-index: -9999 !important;
  position: absolute !important;
  left: -9999px !important;
  top: -9999px !important;
  width: 0 !important;
  height: 0 !important;
  overflow: hidden !important;
  pointer-events: none !important;
  transform: scale(0) !important;
}

/* Show GeoGPT normally when not in fullscreen */
.tab-viewport:not(.fullscreen) ~ .geogpt-fixed-container,
.tab-viewport:not(.fullscreen) ~ * .geogpt-fixed-container,
.tab-viewport:not(.fullscreen) .geogpt-fixed-container,
.geogpt-fixed-container {
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
  z-index: 1000 !important;
  position: fixed !important;
  left: auto !important;
  top: auto !important;
  width: auto !important;
  height: auto !important;
  overflow: visible !important;
  pointer-events: auto !important;
  transform: scale(1) !important;
}

/* Make tab buttons more visible in fullscreen */
.results-tab-bar.fullscreen button {
  font-size: 16px !important;
  padding: 10px 20px !important;
  min-width: 150px !important;
}

.results-tab-bar.fullscreen .tab-buttons-container {
