  pointer-events: none !important;
}

/* Show zoom controls only when not in fullscreen */
.tab-viewport:not(.fullscreen) ~ .map-container .leaflet-control-zoom,
.tab-viewport:not(.fullscreen) .leaflet-control-zoom {
  z-index: 998 !important;
  opacity: 1 !important;
  visibility: visible !important;
  pointer-events: auto !important;
}

/* Floating Close Button for Fullscreen */
.floating-close-btn {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 10000;
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  border: 2px solid rgba(239, 68, 68, 0.3);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 20px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 20px rgba(239, 68, 68, 0.4);
  animation: float-pulse 2s infinite;
}

@keyframes float-pulse {
  0% {
    transform: translateY(0px);
    box-shadow: 0 4px 20px rgba(239, 68, 68, 0.4);
  }
  50% {
    transform: translateY(-3px);
    box-shadow: 0 8px 30px rgba(239, 68, 68, 0.6);
  }
  100% {
    transform: translateY(0px);
    box-shadow: 0 4px 20px rgba(239, 68, 68, 0.4);
  }
}

.floating-close-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.floating-close-btn:hover::before {
  opacity: 1;
}

.floating-close-btn:hover {
  background: linear-gradient(135deg, #dc2626 0%, #ef4444 100%);
  transform: scale(1.1);
  box-shadow: 0 8px 35px rgba(239, 68, 68, 0.6);
}

.floating-close-btn:active {
  transform: scale(0.95);
  box-shadow: 0 4px 20px rgba(239, 68, 68, 0.4);
}

/* Tooltip for floating close button */
.floating-close-btn::after {
  content: attr(title);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.9);
  color: white;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  margin-bottom: 10px;
  z-index: 10001;
  pointer-events: none;
}

.floating-close-btn:hover::after {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%) translateY(-3px);
}

