.status-label { font-weight: 900; font-size: 10px; letter-spacing: 1px; color: var(--status-color); }

.live-dot {
    width: 6px; height: 6px;
    background: #ef4444;
    border-radius: 50%;
    animation: blink 1s infinite;
}

/* --- MAIN BODY --- */
.cnn-main-body {
    display: flex;
    gap: 20px;
    padding: 20px;
}

.cnn-viewfinder {
    position: relative;
    width: 130px; height: 130px;
    background: #000;
    padding: 4px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.cnn-feed {
    width: 100%; height: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
    overflow: hidden;
}

/* --- INFO SECTION --- */
.cnn-info-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.classification-name {
    font-size: 24px;
    font-weight: 900;
    text-transform: uppercase;
    margin: 4px 0;
    letter-spacing: 1px;
}

.stat-block label {
    font-size: 9px;
    letter-spacing: 1px;
    opacity: 0.6;
    font-weight: 700;
}

.confidence-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 4px;
}

.tactical-progress-track {
    height: 6px;
    background: rgba(128, 128, 128, 0.1);
    border-radius: 10px;
    overflow: hidden;
}

.tactical-progress-fill {
    height: 100%;
    transition: width 1s ease-in-out;
    box-shadow: 0 0 10px var(--status-glow);
}

/* --- BOXES --- */
.cnn-warning-box, .cnn-success-box {
    display: flex;
    gap: 10px;
    padding: 10px;
    border-radius: 6px;
    font-size: 11px;
    line-height: 1.4;
    border: 1px solid transparent;
}

.cnn-warning-box { background: rgba(245, 158, 11, 0.1); border-color: rgba(245, 158, 11, 0.2); color: #f59e0b; }
.cnn-success-box { background: rgba(16, 185, 129, 0.1); border-color: rgba(16, 185, 129, 0.2); color: #10b981; }

/* --- FOOTER --- */
.cnn-footer-telemetry {
    display: flex;
    justify-content: space-between;
    padding: 10px 16px;
    background: rgba(0, 0, 0, 0.1);
    font-family: monospace;
    font-size: 9px;
    opacity: 0.6;
    border-top: 1px solid var(--border);
}

.t-item span { color: var(--accent); font-weight: 900; margin-right: 4px; }

/* --- CROSSHAIRS --- */
.view-corner { position: absolute; width: 10px; height: 10px; border: 2px solid var(--status-color); }
.tl { top: -2px; left: -2px; border-right: 0; border-bottom: 0; }
.tr { top: -2px; right: -2px; border-left: 0; border-bottom: 0; }
.bl { bottom: -2px; left: -2px; border-right: 0; border-top: 0; }
.br { bottom: -2px; right: -2px; border-left: 0; border-top: 0; }

@keyframes blink { 0%, 100% { opacity: 1; } 50% { opacity: 0.3; } }

/* --- COMPONENT LAYOUT --- */
.cnn-tactical-card {
  padding: 20px !important;
  border: 1px solid var(--border) !important;
  position: relative;
}

.cnn-tactical-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.tactical-header-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 6px;
}

/* --- SPECIAL ENGINE BADGE --- */
.model-id-badge {
  display: flex;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
}

.model-label {
  background: var(--accent);
  color: white;
  font-size: 8px;
  font-weight: 900;
  padding: 2px 6px;
  display: flex;
  align-items: center;
}

.model-name {
  color: #fff;
  font-family: monospace;
  font-size: 9px;
  padding: 2px 8px;
  font-weight: 700;
}

/* --- STATUS PILL --- */
.status-indicator-pill {
  font-size: 9px;
  font-weight: 900;
  padding: 3px 10px;
  border-radius: 2px;
  color: #000;
  display: flex;
  align-items: center;
  gap: 4px;
}

/* --- VISUAL FRAME --- */
.cnn-frame {
  position: relative;
  width: 130px;
  height: 130px;
  background: #000;
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 4px;
}

.corner-bit {
  position: absolute;
  width: 8px;
  height: 8px;
  border: 2px solid var(--status-color);
}
.corner-bit.tl { top: -2px; left: -2px; border-right: 0; border-bottom: 0; }
.corner-bit.tr { top: -2px; right: -2px; border-left: 0; border-bottom: 0; }
.corner-bit.bl { bottom: -2px; left: -2px; border-right: 0; border-top: 0; }
.corner-bit.br { bottom: -2px; right: -2px; border-left: 0; border-top: 0; }

/* --- ALERT BOXES --- */
.cnn-alert-box {
  padding: 10px;
  border-radius: 4px;
  border-left: 3px solid transparent;
  font-size: 11px;
}

.cnn-alert-box.warning {
  background: rgba(245, 158, 11, 0.1);
  border-left-color: #f59e0b;
  color: #fcd34d;
}

.cnn-alert-box.success {
  background: rgba(16, 185, 129, 0.1);
  border-left-color: #10b981;
  color: #6ee7b7;
}

.cnn-alert-box p {
  margin: 2px 0 0 0;
  opacity: 0.8;
  font-size: 10px;
}

/* --- LIGHT THEME FIXES (Darker Yellow) --- */
[data-theme="light"] .model-id-badge {
  background: rgba(0, 0, 0, 0.05);
  border-color: rgba(0, 0, 0, 0.1);
}

[data-theme="light"] .model-name {
  color: #334155;
}

[data-theme="light"] .cnn-alert-box.warning {
  background: #fffbeb;
  border-color: #d97706; /* Darker Gold for visibility */
  color: #92400e;        /* Deep Brownish-Yellow */
}

[data-theme="light"] .cnn-alert-box.success {
  background: #f0fdf4;
  border-color: #16a34a;
  color: #166534;
}

[data-theme="light"] .status-indicator-pill {
  color: #fff; /* White text for better contrast on badges in light mode */
}


/* --- UPDATED FOOTER TELEMETRY --- */
.cnn-footer-telemetry {
  display: flex;
  justify-content: space-between;
  padding: 12px 16px;
  background: rgba(128, 128, 128, 0.05); /* Very subtle background tint */
  font-family: 'Share Tech Mono', monospace; /* Professional tech font */
  font-size: 10px;
  font-weight: 600;
  letter-spacing: 0.5px;
  border-top: 1px solid var(--border);
  margin: 15px -20px -20px -20px; /* Pulls footer to the edges of the card */
}

/* --- THEME-SPECIFIC COLOR LOGIC --- */
[data-theme="dark"] .cnn-footer-telemetry {
  color: #ffffff !important; /* Force White in Dark Theme */
  background: rgba(0, 0, 0, 0.2);
}

[data-theme="light"] .cnn-footer-telemetry {
  color: #000000 !important; /* Force Black in Light Theme */
  background: rgba(0, 0, 0, 0.03);
}

/* Optional: Make the labels (RES, SENSOR) slightly bolder or accented */
.cnn-footer-telemetry span {
  display: flex;
  align-items: center;
}

/* Target the specific stat item for classification */
.cnn-stat-item {
  display: flex;
  flex-direction: column; /* This forces children to stack vertically */
  align-items: flex-start; /* Aligns both label and value to the left */
  gap: 4px; /* Adds a clean gap between the label and the value */
}

.cnn-stat-item label {
  display: block; /* Ensures the label takes its own row */
  width: 100%;
}

.cnn-class-text {
  display: block; /* Ensures "Water" or "Forest" drops to the next line */
  margin-top: 2px;
  font-size: 26px; /* High-visibility sizing */
  line-height: 1.1;
}


.mini-map-context {
  height: 220px; /* Adjust based on your card size */
  width: 100%;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 15px;
}

.mini-map-label {
