  border-radius: 4px;
  z-index: 10;
  letter-spacing: 1px;
}


/* --- PRO 3D MAP WRAPPER --- */
.pro-map-container {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* --- TACTICAL HUD STYLE SWITCHER --- */
.map-type-hud {
  position: absolute;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  background: rgba(15, 23, 42, 0.75); /* Dark Slate with transparency */
  backdrop-filter: blur(12px) saturate(180%); /* Pro Glassmorphism */
  -webkit-backdrop-filter: blur(12px);
  padding: 8px;
  border-radius: 14px;
  border: 1px solid rgba(0, 212, 255, 0.2);
  z-index: 100;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
}

.map-type-hud button {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #94a3b8;
  padding: 8px 14px;
  font-family: 'JetBrains Mono', 'Fira Code', monospace;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.map-type-hud button:hover {
  background: rgba(0, 212, 255, 0.1);
  color: #fff;
  border-color: rgba(0, 212, 255, 0.4);
}

.map-type-hud button.active {
  background: #00d4ff; /* Tactical Cyan */
  color: #0f172a;
  border-color: #00d4ff;
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.4);
  opacity: 1;
}

/* --- PULSING 3D MARKER --- */
.pulse-marker-3d {
  width: 20px;
  height: 20px;
  background: #00d4ff;
  border: 3px solid #ffffff;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.8);
  position: relative;
}

/* Outer pulsing rings */
.pulse-marker-3d::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 2px solid #00d4ff;
  animation: geo-scan-pulse 2s infinite ease-out;
}

@keyframes geo-scan-pulse {
  0% {
    width: 20px;
    height: 20px;
    opacity: 1;
  }
  100% {
    width: 60px;
    height: 60px;
    opacity: 0;
  }
}

/* --- TOP HUD INDICATOR --- */
.map-hud {
  position: absolute;
  top: 16px;
  left: 16px;
  background: rgba(15, 23, 42, 0.8);
  color: #00d4ff;
  padding: 6px 12px;
  font-family: 'Courier New', monospace;
  font-size: 10px;
  font-weight: bold;
  border-left: 4px solid #00d4ff;
  border-radius: 4px;
  pointer-events: none;
  z-index: 10;
  letter-spacing: 2px;
  text-shadow: 0 0 8px rgba(0, 212, 255, 0.5);
  animation: flicker-hud 4s infinite;
}

@keyframes flicker-hud {
  0%, 100% { opacity: 1; }
  92% { opacity: 1; }
  93% { opacity: 0.7; }
  94% { opacity: 1; }
}

