/* --- UNIFIED CONTAINER LOGIC --- */
/* Pins both separate JS containers to the same top-right corner to prevent overlap */
.engine-switch-container,
.map-variety-picker {
  position: absolute;
  right: 20px;
  width: 200px;
  z-index: 2000;
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.4);
  font-family: 'Inter', sans-serif;
  transition: all 0.3s ease;
}
.engine-switch-container{
  width: 100px!important;
}

/* Layer 1: The 2D/3D Switch */
.engine-switch-container {
  top: 20px;
  display: flex;
  padding: 2px;
  background: rgba(15, 23, 42, 0.8);
}

/* Layer 2: The Style Picker & Spectral Buttons (Stacked below Switch) */
.map-variety-picker {
  top: 50px;
/* Positioned exactly below the first container */
  padding: 8px;
  background: rgba(15, 23, 42, 0.6);
  display: flex;
  flex-direction: column;
  gap: 3px;
}

/* --- THEME SPECIFIC OVERRIDES --- */
[data-theme="light"] .engine-switch-container,
[data-theme="light"] .map-variety-picker {
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.1);
}

/* --- BUTTON STYLING --- */
.switch-btn, .spectral-toggle-bar button {
  flex: 1;
  border: none;
  background: transparent;
  cursor: pointer;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 700;
  transition: all 0.3s ease;
  border-radius: 8px;
}

/* 2D/3D Active State */
.switch-btn {
  color: #94a3b8;
  font-size: 11px;
  padding: 4px 0;
}

.switch-btn.active {
  background: #00d4ff; /* Tactical Cyan */
  color: #001a2c;
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.4);
}

/* --- SPECTRAL BUTTONS (NDVI, HEAT, FLOW) --- */
.spectral-toggle-bar {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 6px;
  margin-top: 5px;
}

.spectral-toggle-bar button {
  padding: 8px 2px;
  font-size: 9px;
  color: var(--text);
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

/* Active Analysis States */
.spectral-toggle-bar button.active:nth-child(1) { /* NDVI - Green */
  background: rgba(16, 185, 129, 0.2) !important;
  color: #10b981 !important;
  border-color: #10b981;
}

.spectral-toggle-bar button.active:nth-child(2) { /* HEAT - Orange */
  background: rgba(245, 158, 11, 0.2) !important;
  color: #f59e0b !important;
  border-color: #f59e0b;
}

.spectral-toggle-bar button.active:nth-child(3) { /* FLOW - Blue */
  background: rgba(59, 130, 246, 0.2) !important;
  color: #3b82f6 !important;
  border-color: #3b82f6;
}

/* --- SELECTOR STYLING --- */
.variety-select {
  width: 100%;
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  padding: 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  outline: none;
}

[data-theme="light"] .variety-select {
  background: #fff;
  color: #000;
  border-color: #ccc;
}

/* Hover effect for visibility */
.switch-btn:hover:not(.active),
.spectral-toggle-bar button:hover:not(.active) {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

[data-theme="light"] .switch-btn:hover:not(.active),
[data-theme="light"] .spectral-toggle-bar button:hover:not(.active) {
  background: rgba(0, 0, 0, 0.05);
  color: #000;
}


/* --- DEEP BLACK OVERRIDES FOR DARK THEME --- */

[data-theme="dark"] .map-variety-picker,
[data-theme="dark"] .engine-switch-container {
  /* Replace Translucent Navy with Pure Black */
  background: rgba(0, 0, 0, 0.85) !important; 
  border: 1px solid rgba(255, 255, 255, 0.15) !important;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.8) !important;
}

[data-theme="dark"] .hud-select,
[data-theme="dark"] .variety-select {
  /* Style the dropdown itself */
  background-color: #000000 !important;
  color: #ffffff !important;
  border: 1px solid rgba(255, 255, 255, 0.2) !important;
}

/* Forces the browser's native dropdown list (options) to be black */
[data-theme="dark"] select option {
  background-color: #000000 !important;
  color: #ffffff !important;
}

/* Ensure Spectral buttons also look good against pure black */
[data-theme="dark"] .spectral-toggle-bar button {
  background: rgba(255, 255, 255, 0.03);
}

[data-theme="dark"] .spectral-toggle-bar button:hover:not(.active) {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

/* --- OPTIONAL: GLOW REFINEMENT --- */
/* Makes the cyan pop more against the pure black background */
[data-theme="dark"] .switch-btn.active {
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.5) !important;
}



/* --- TACTICAL ZOOM (Left Side) --- */
.tactical-zoom-hud {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.85); /* Deep black glass */
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 8px;
  z-index: 2200;
}

.tactical-zoom-hud button {
  width: 32px;
  height: 32px;
  background: transparent;
  border: none;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s;
}

.tactical-zoom-hud button:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #00d4ff;
}

.zoom-divider {
  height: 1px;
  background: rgba(255, 255, 255, 0.1);
