  margin: 0 auto;
}

/* --- FULLSCREEN INTEGRATION --- */
.vertical-divider {
  width: 1px;
  height: 16px;
  background: rgba(255, 255, 255, 0.2);
  margin: 0 4px;
  align-self: center;
}

.fullscreen-btn {
