/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Ensure the analysis viewport covers map controls when expanded */
.tab-viewport.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 9999; /* Higher than Leaflet controls and HUD */
    background: var(--bg-primary, #0a0a0a); /* Ensure no transparency */
    overflow-y: auto;
}

/* Hide the tactical zoom HUD specifically when analysis is fullscreen */
body:has(.tab-viewport.fullscreen) .tactical-zoom-hud,
body:has(.tab-viewport.fullscreen) .engine-switch-container,
body:has(.tab-viewport.fullscreen) .map-variety-picker {
    display: none !important;
}

/* Ensure the close button inside fullscreen is visible and on top */
.floating-close-btn {
    z-index: 10000;
}
/* Fullscreen Close Button Styling */
.floating-close-btn {
    position: fixed !important;
    top: 20px !important;
    right: 20px !important;
    width: 45px !important;
    height: 45px !important;
    background: rgba(239, 68, 68, 0.9) !important; /* Vivid Red */
    color: white !important;
    border: 2px solid rgba(255, 255, 255, 0.2) !important;
    border-radius: 12px !important;
    font-size: 24px !important;
    font-weight: bold !important;
    cursor: pointer !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    z-index: 10001 !important; /* Must be higher than the 9999 viewport */
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
    box-shadow: 0 0 15px rgba(239, 68, 68, 0.4) !important;
    backdrop-filter: blur(4px) !important;
}

.floating-close-btn:hover {
    background: #ef4444 !important; /* Solid Red */
    transform: scale(1.1) rotate(90deg) !important;
    box-shadow: 0 0 25px rgba(239, 68, 68, 0.8) !important;
    border-color: white !important;
}

.floating-close-btn:active {
    transform: scale(0.9) !important;
}

/* Redundancy Cleanup: Ensure everything else is hidden behind the analysis */
.tab-viewport.fullscreen {
    z-index: 9999 !important;
    background-color: #0a0a0a !important; /* Prevents map bleed-through */
    padding-top: 60px !important; /* Space for the floating button */
}

/* Terrain & Slope Analysis Card - Compact Professional Design */
.terrain-card {
  background: linear-gradient(135deg, rgba(34, 197, 94, 0.08), rgba(59, 130, 246, 0.08));
  border: 1px solid rgba(34, 197, 94, 0.2);
  border-radius: 16px;
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.terrain-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(34, 197, 94, 0.1), transparent);
  animation: shimmer 3s ease-in-out infinite;
}

.terrain-score-badge {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(34, 197, 94, 0.15);
  border: 1px solid rgba(34, 197, 94, 0.3);
  border-radius: 12px;
  padding: 8px 12px;
  min-width: 60px;
  flex-shrink: 0;
}

.terrain-score-badge .score-value {
  font-size: 0.9rem !important;
  font-weight: 700 !important;
  color: #22c55e !important;
  line-height: 1;
}

.terrain-score-badge .score-label {
  font-size: 0.55rem !important;
  font-weight: 600 !important;
  letter-spacing: 0.3px !important;
  color: #86efac !important;
  margin-top: 2px !important;
}

