/* GeoGPT Intelligence Assistant - Enhanced Multi-LLM Version */

/* Main Container */
.geogpt-polished-box {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 380px;
  height: 600px;
  background: rgba(15, 23, 42, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  z-index: 1000;
  transition: all 0.3s ease;
}

/* Light Theme */
[data-theme='light'] .geogpt-polished-box {
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.15);
}

/* Pill Button (Collapsed State) */
.geogpt-pill-btn {
  position: fixed;
  bottom: 30px;
  right: 30px;
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  border: none;
  border-radius: 50px;
  padding: 12px 24px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  box-shadow: 0 10px 25px -5px rgba(59, 130, 246, 0.5);
  transition: all 0.3s ease;
  z-index: 999;
}

.geogpt-pill-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 35px -5px rgba(59, 130, 246, 0.6);
}

.gpt-icon-glow {
  font-size: 16px;
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.7; transform: scale(1.1); }
}

.gpt-label {
  font-size: 14px;
}

/* Header */
.geogpt-chat-header {
  padding: 16px 20px;
  background: rgba(59, 130, 246, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
}

[data-theme='light'] .geogpt-chat-header {
  background: rgba(59, 130, 246, 0.05);
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.gpt-status {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.provider-indicators {
  display: flex;
  gap: 6px;
}

.provider-mini-indicator {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  font-size: 10px;
}

[data-theme='light'] .provider-mini-indicator {
  background: rgba(0, 0, 0, 0.05);
}

.provider-mini-icon {
  font-size: 10px;
}

.provider-mini-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

.provider-mini-dot.online {
  background: #10b981;
  box-shadow: 0 0 4px #10b981;
}

.provider-mini-dot.offline {
  background: #64748b;
}

.gpt-status strong {
  color: #3b82f6;
  font-size: 14px;
  font-weight: 600;
}

[data-theme='light'] .gpt-status strong {
  color: #1e40af;
}

.current-provider {
  font-size: 12px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 4px;
}

.chat-controls {
  display: flex;
  gap: 8px;
}

.minimize-gpt,
.close-gpt {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 8px;
  width: 28px;
  height: 28px;
  color: white;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

[data-theme='light'] .minimize-gpt,
[data-theme='light'] .close-gpt {
  background: rgba(0, 0, 0, 0.05);
  color: #64748b;
}

.minimize-gpt:hover,
.close-gpt:hover {
  background: rgba(255, 255, 255, 0.2);
}

[data-theme='light'] .minimize-gpt:hover,
[data-theme='light'] .close-gpt:hover {
  background: rgba(0, 0, 0, 0.1);
}

/* Messages Container */
.geogpt-messages {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.geogpt-messages::-webkit-scrollbar {
  width: 6px;
}

.geogpt-messages::-webkit-scrollbar-track {
  background: transparent;
}

.geogpt-messages::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 3px;
}

[data-theme='light'] .geogpt-messages::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
}

/* Message Bubbles */
.chat-msg {
  display: flex;
  max-width: 100%;
}

.chat-msg.user {
  justify-content: flex-end;
}

.chat-msg.assistant {
  justify-content: flex-start;
}

.msg-bubble {
  max-width: 85%;
  padding: 12px 16px;
  border-radius: 16px;
  position: relative;
}

.chat-msg.user .msg-bubble {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  color: white;
  border-bottom-right-radius: 4px;
}

.chat-msg.assistant .msg-bubble {
  background: rgba(255, 255, 255, 0.1);
  color: #e2e8f0;
  border-bottom-left-radius: 4px;
}

[data-theme='light'] .chat-msg.assistant .msg-bubble {
  background: rgba(0, 0, 0, 0.05);
  color: #475569;
}

.msg-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
  font-size: 11px;
  opacity: 0.8;
}

.msg-provider-info {
  display: flex;
  align-items: center;
  gap: 6px;
}

.provider-icon {
  font-size: 12px;
}

.provider-name {
  font-weight: 600;
}

.confidence {
  background: rgba(16, 185, 129, 0.2);
  color: #10b981;
  padding: 2px 6px;
  border-radius: 8px;
  font-size: 10px;
}

.response-time {
  color: #94a3b8;
  font-size: 10px;
}

.msg-content {
  line-height: 1.5;
}

.msg-content p {
  margin: 0 0 8px 0;
}

.msg-content p:last-child {
  margin-bottom: 0;
}

.msg-content code {
  background: rgba(255, 255, 255, 0.1);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  font-size: 12px;
}

[data-theme='light'] .msg-content code {
  background: rgba(0, 0, 0, 0.1);
}

.msg-content pre {
  background: rgba(255, 255, 255, 0.05);
  padding: 8px;
  border-radius: 8px;
  overflow-x: auto;
  font-size: 11px;
}

[data-theme='light'] .msg-content pre {
  background: rgba(0, 0, 0, 0.05);
}

/* Sources */
.sources {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  gap: 6px;
  flex-wrap: wrap;
}

[data-theme='light'] .sources {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.sources-label {
  font-size: 10px;
  opacity: 0.7;
}

.source-tag {
  background: rgba(59, 130, 246, 0.2);
  color: #3b82f6;
  padding: 2px 6px;
  border-radius: 8px;
  font-size: 10px;
  font-weight: 500;
}

/* Comparison Button */
.show-comparison-btn {
  background: rgba(16, 185, 129, 0.2);
  border: none;
  border-radius: 8px;
  padding: 6px 12px;
  color: #10b981;
  font-size: 11px;
  cursor: pointer;
  margin-top: 8px;
  transition: all 0.2s ease;
}

.show-comparison-btn:hover {
  background: rgba(16, 185, 129, 0.3);
}

/* Typing Indicator */
.typing-indicator {
  display: flex;
  gap: 4px;
  margin-right: 8px;
}

.typing-indicator span {
  width: 6px;
  height: 6px;
  background: #3b82f6;
  border-radius: 50%;
  animation: typing 1.4s ease-in-out infinite;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%, 60%, 100% { transform: translateY(0); }
  30% { transform: translateY(-10px); }
}

.thinking-bubble {
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Comparison Panel */
.comparison-panel {
  margin: 12px;
  padding: 12px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
}

[data-theme='light'] .comparison-panel {
  background: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.comparison-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.comparison-header h4 {
  margin: 0;
  color: #3b82f6;
  font-size: 14px;
}

[data-theme='light'] .comparison-header h4 {
  color: #1e40af;
}

.close-comparison {
  background: none;
  border: none;
  color: #94a3b8;
  font-size: 16px;
  cursor: pointer;
  padding: 4px;
}

.comparison-table {
  font-size: 12px;
}

.comparison-table h4 {
  margin: 0 0 8px 0;
  color: #e2e8f0;
  font-size: 13px;
}

[data-theme='light'] .comparison-table h4 {
  color: #475569;
}

.comparison-table table {
  width: 100%;
  border-collapse: collapse;
}

.comparison-table th,
.comparison-table td {
  padding: 6px 8px;
  text-align: left;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

[data-theme='light'] .comparison-table th,
[data-theme='light'] .comparison-table td {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.comparison-table th {
  background: rgba(59, 130, 246, 0.1);
  color: #3b82f6;
  font-weight: 600;
}

[data-theme='light'] .comparison-table th {
  background: rgba(59, 130, 246, 0.05);
}

.comparison-table td {
  color: #e2e8f0;
}

[data-theme='light'] .comparison-table td {
  color: #475569;
}

/* Quick Questions */
.quick-questions {
  padding: 12px 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

[data-theme='light'] .quick-questions {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.quick-questions-label {
  font-size: 11px;
  color: #94a3b8;
  margin-bottom: 8px;
}

[data-theme='light'] .quick-questions-label {
  color: #64748b;
}

.quick-questions-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 6px;
}

.quick-question-btn {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 6px 8px;
  color: #e2e8f0;
  font-size: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
}

[data-theme='light'] .quick-question-btn {
  background: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.1);
  color: #64748b;
}

.quick-question-btn:hover {
  background: rgba(59, 130, 246, 0.1);
  border-color: rgba(59, 130, 246, 0.3);
}

/* Input Area */
.geogpt-input-area {
  padding: 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  flex-shrink: 0;
}

[data-theme='light'] .geogpt-input-area {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.input-wrapper {
  display: flex;
  gap: 8px;
  align-items: center;
}

.input-wrapper input {
  flex: 1;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  padding: 10px 14px;
  color: white;
  font-size: 13px;
  outline: none;
  transition: all 0.2s ease;
}

[data-theme='light'] .input-wrapper input {
  background: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(0, 0, 0, 0.1);
  color: #1e293b;
}

.input-wrapper input::placeholder {
  color: #94a3b8;
}

[data-theme='light'] .input-wrapper input::placeholder {
  color: #94a3b8;
}

.input-wrapper input:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.2);
}

.input-wrapper input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.send-button {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  border: none;
  border-radius: 12px;
  width: 40px;
  height: 40px;
  color: white;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.send-button:hover:not(:disabled) {
  transform: scale(1.05);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
}

.send-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.loading-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Enhanced GeoGPT with Extensible Sidebar */

.geogpt-content {
  display: flex;
  height: calc(100% - 60px); /* Subtract header height */
  overflow: hidden;
}

/* Extensible Sidebar */
.geogpt-sidebar {
  width: 0;
  background: rgba(255, 255, 255, 0.05);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  transition: width 0.3s ease;
  overflow: hidden;
  flex-shrink: 0;
}

[data-theme='light'] .geogpt-sidebar {
  background: rgba(0, 0, 0, 0.03);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.geogpt-sidebar.extended {
  width: 280px;
}

.sidebar-header {
  padding: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 60px;
}

[data-theme='light'] .sidebar-header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.sidebar-header h4 {
  margin: 0;
  color: #3b82f6;
  font-size: 14px;
  font-weight: 600;
}

[data-theme='light'] .sidebar-header h4 {
  color: #1e40af;
}

.sidebar-collapse-btn {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 6px;
  width: 24px;
  height: 24px;
  color: #94a3b8;
  font-size: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

[data-theme='light'] .sidebar-collapse-btn {
  background: rgba(0, 0, 0, 0.05);
  color: #64748b;
}

.sidebar-collapse-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

[data-theme='light'] .sidebar-collapse-btn:hover {
  background: rgba(0, 0, 0, 0.1);
}

.sidebar-content {
  padding: 0 16px 16px;
  overflow-y: auto;
  height: calc(100% - 60px);
}

.sidebar-section {
  margin-bottom: 20px;
}

.sidebar-section h5 {
  margin: 0 0 12px 0;
  color: #e2e8f0;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

[data-theme='light'] .sidebar-section h5 {
  color: #475569;
}

.sidebar-actions {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.sidebar-action-btn {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 10px 12px;
  color: #e2e8f0;
  font-size: 11px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
  font-weight: 500;
}

[data-theme='light'] .sidebar-action-btn {
  background: rgba(0, 0, 0, 0.03);
  border: 1px solid rgba(0, 0, 0, 0.1);
  color: #64748b;
}

.sidebar-action-btn:hover {
  background: rgba(59, 130, 246, 0.1);
  border-color: rgba(59, 130, 246, 0.3);
  transform: translateX(2px);
}

.sidebar-action-btn.site-analysis {
  background: rgba(16, 185, 129, 0.1);
  border-color: rgba(16, 185, 129, 0.3);
  color: #10b981;
}

[data-theme='light'] .sidebar-action-btn.site-analysis {
  background: rgba(16, 185, 129, 0.05);
  color: #059669;
}

.sidebar-action-btn.site-analysis:hover {
  background: rgba(16, 185, 129, 0.2);
}

/* Main Chat Area */
.geogpt-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.geogpt-content.sidebar-extended .geogpt-main {
  max-width: calc(100% - 280px);
}

/* Sidebar Toggle Button */
.sidebar-toggle-btn {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 6px;
  width: 32px;
  height: 32px;
  color: #94a3b8;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  margin-right: 8px;
}

[data-theme='light'] .sidebar-toggle-btn {
  background: rgba(0, 0, 0, 0.05);
  color: #64748b;
}

.sidebar-toggle-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

[data-theme='light'] .sidebar-toggle-btn:hover {
  background: rgba(0, 0, 0, 0.1);
}

.sidebar-toggle-btn.extended {
  background: rgba(59, 130, 246, 0.2);
  color: #3b82f6;
}

/* Quick Questions Toggle Button */
.quick-questions-toggle {
  padding: 8px 16px;
  text-align: center;
}

.show-quick-questions-btn {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 20px;
  padding: 8px 16px;
  color: #3b82f6;
  font-size: 11px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
}

[data-theme='light'] .show-quick-questions-btn {
  background: rgba(59, 130, 246, 0.05);
  color: #1e40af;
}

.show-quick-questions-btn:hover {
  background: rgba(59, 130, 246, 0.2);
  transform: translateY(-1px);
}

/* Enhanced Chat Controls */
.chat-controls {
  display: flex;
  gap: 6px;
  align-items: center;
}

/* Responsive Design */
@media (max-width: 600px) {
  .geogpt-sidebar.extended {
    width: 240px;
  }
  
  .geogpt-content.sidebar-extended .geogpt-main {
    max-width: calc(100% - 240px);
  }
  
  .sidebar-action-btn {
    font-size: 10px;
    padding: 8px 10px;
  }
  
  .sidebar-header h4 {
    font-size: 12px;
  }
}
